* {
  box-sizing: border-box;
  color: var(--text);
  margin: 0;
}

body {
  background-color: var(--body);
}

:root {
  --body:#445175;
  --tr:#3d486c;
  --case:#ff9d00;
  --death:#f64e63;
  --recovered:#65dd9b;
  --title:#dfdfef;
  --text:#fafafb;
  --border:#5e698b;
}

.header {
  overflow: hidden;
}
.header h1 {
  color: var(--title);
  margin-top: 1.875rem;
  text-align: center;
}
.header .search {
  float: right;
  margin: 1.875rem 9.375rem;
}

table {
  width: 85%;
  margin-left: 6.25rem;
  background-color: var(--tr);
  text-align: center;
}
table td {
  border: 2px solid var(--border);
  width: 13.75rem;
  height: 3.125rem;
  font-weight: 600;
}
table th {
  border: 2px solid var(--border);
  width: 13.75rem;
  height: 3.125rem;
}

.global-btn {
  text-align: center;
}

button {
  color: black;
  border-radius: 0.4375rem;
  height: 1.5625rem;
}

input {
  color: black;
}

.text {
  margin: 1.875rem 0;
  text-align: center;
}

#country {
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 900;
  font-size: 1.25rem;
}

#case {
  color: var(--case);
}

#deaths {
  color: var(--death);
}

#recovered {
  color: var(--recovered);
}

#tb-2 #country {
  border: none;
}